@import "components/all";
@import "./libs/_normalize";
@import "fonts";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}
.form {
  margin: 0 auto;
  padding: 100px 30px 0;

  @media (min-width: 800px) {
    max-width: 60%;
  }
}

.input-range {
  margin-bottom: 160px;
}

.wrapper {
  width: calc(100vw);
  height: 100%;
  background-color: #fff;
  margin: 0 auto;
  border-radius: 10px;
  max-width: 1440px;
  overflow: visible;
}

.container {
  margin: 213px 47px 50px 47px;
  @media (max-width: 1024px) {
    margin-top: 100px;
  }
  @media (max-width: 768px) {
    margin: 64px 36px;
  }
  @media (max-width: 320px) {
    margin: 44px 20px;
  }

  &__input {
    display: grid;
    grid-gap: 2vw;
    justify-items: center;
    justify-content: space-between;
    grid-template-rows: 1fr;
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 49px;
    @media (max-width: 1024px) {
      grid-template-columns: 1fr;
    }
  }

  &__bottom {
    display: grid;
    grid-gap: 2vw;
    justify-items: start;
    justify-content: space-between;
    grid-template-rows: 1fr;
    grid-template-columns: repeat(3, 1fr);
    @media (max-width: 1024px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 320px) {
      grid-template-columns: 1fr
    }

  }
}

.title {
  font-size: 54px;
  max-width: 758px;
  font-weight: 900;
  margin-bottom: 32px;
  @media (max-width: 768px) {
    min-width: 612px;
  }
  @media (max-width: 320px) {
    font-size: 34px;
    min-width: 320px;
  }
}