
.inputBox {
  position: relative;
  width: 100%;
  height: 119px;
  @media (max-width: 1024px) {
    margin-bottom: 32px;
  }
  @media (max-width: 320px) {
    margin-bottom: 16px;
  }

  &__inputNumber {
    width: 100%;
    height: 68px;
    margin-top: 20px;
    background-color: #f3f3f4;
    border: none;
    border-radius: 16px;
    padding: 0 24px;
    font-size: 30px;
    color: #575757;
    font-weight: 900;

    &:focus {
      background-color: #fff;
      border: 2px solid #f3f3f4;
    }

    &:hover {
      background-color: #f3f3f4;
    }

    &:disabled {
      opacity: 0.4;
    }

    @media (max-width: 768px) {
      min-width: 612px;
    }
    @media (max-width: 320px) {
      min-width: 280px;
      font-size: 22px;
    }


  }

  &__inputNumberPayment {
    position: absolute;
    right: 7px;
    bottom: 1px;
    width: 70px !important;
    height: 54px;
    background-color: #EBEBEC;
    border: none;
    border-radius: 16px;
    padding: 0 17px;
    font-size: 20px;
    color: #575757;
    font-weight: 900;

    &:focus {
      background-color: #fff;
      border: 2px solid #f3f3f4;
    }

    &:hover {
      background-color: #f3f3f4;
    }

    &:disabled {
      opacity: 0.4;
    }

    @media (max-width: 320px) {
      font-size: 22px;
    }

  }

  &__inputRange {
    position: absolute;
    width: 88%;
    height: 2px;
    top: 124px;
    left: 0;
    margin: 0 24px;
    z-index: 5;
    accent-color: #FF9514;

    &:disabled {
      opacity: 0.4;
    }

  }

  &__title {
    font-family: "Gilroy", Roboto, system-ui, Tahoma, sans-serif;
    font-size: 16px;
    color: #575757;
    @media (max-width: 320px) {
      font-size: 14px;
    }
  }

  &__paymentCurrent {
    position: absolute;
    top: 79px;
    right: 20px;
    font-weight: 900;
    font-size: 20px;
    color: #575757;

    span {
      color: #575757;
      vertical-align: 3px;
    }
  }

  &__percentBlock {
    width: 100%;
    height: 69px;
    background-color: #f3f3f4;
    border-radius: 16px;
    font-weight: 900;
    font-size: 30px;
    color: #575757;
    padding: 14px 24px;


    span {
      color: #575757;
      vertical-align: 3px;
      @media (max-width: 320px) {
        vertical-align: 8px;
      }
    }

    @media (max-width: 320px) {
      font-size: 22px;
    }
  }

  &__currency {
    position: absolute;
    color: #575757;
    font-size: 30px;
    bottom: 10px;
    right: 24px;
    z-index: 10;
    font-weight: 900;

  }
}
