
.button {
  position: relative;
  width: 100%;
  min-width: 285px;
  height: 68px;
  border-radius: 40px;
  font-size: 30px;
  font-weight: 900;
  border: none;
  background-color: #FF9514;
  color: white;
  @media (max-width: 1024px) {
    width: 80%;
  }
  @media (max-width: 320px) {
    font-size: 22px;
    height: 60px;
    width: 280px;
  }

  &:hover {
    background-color: #111111;
  }

  &:active {
    background-color: #575757;
  }

  &:disabled {
    opacity: 0.4;

    &:hover {
      background-color: #FF9514;
    }

    &:active {
      background-color: #FF9514;
    }
  }

}

.spinner {
  animation: rotate 2s linear infinite;
  z-index: 2;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -20px 0 0 -20px;
  width: 40px;
  height: 40px;

  & .path {
    stroke: white;
    stroke-linecap: round;
    animation: dash 1.5s ease-in-out infinite;
  }

}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes dash {
  0% {
    stroke-dasharray: 1, 150;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -35;
  }
  100% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -124;
  }
}