.textBox {
  color: #575757;
  min-width: 300px;
  @media (max-width: 1024px) {
    margin-bottom: 44px;
  }

  &__title {
    font-family: "Gilroy", Roboto, system-ui, Tahoma, sans-serif;
    font-size: 16px;
    @media (max-width: 320px) {
      font-size: 14px;
    }
  }

  &__price {
    font-size: 54px;
    font-weight: 900;
    @media (max-width: 320px) {
      font-size: 22px;
    }

    span {
      color: #575757;
      vertical-align: 5px;
      @media (max-width: 320px) {
        vertical-align: 2px;
      }
    }
  }
}